import "primeicons/primeicons.css";
import "primereact/resources/themes/saga-blue/theme.css";
import "primereact/resources/primereact.css";
import "primeflex/primeflex.css";
import "../index.css";
import React, { Component } from "react";
import { Button } from "primereact/button";
import { InputText } from "primereact/inputtext";
import SearchAnalyticsTable from "./SearchAnalyticsTable";
import { Toast } from "primereact/toast";

class UploadFile extends Component {
  constructor(props) {
    super(props);

    this.state = {
      isSubmittedForUpload : false,
      date10: "",
      fileNa: "",
      varll:""
    };
  }

  sendMonth = (e) => {
    this.state.date10 = e.target.value
    console.log(this.state.date10)
  }

  sendFile = (e) => {
    console.log(e.target.files[0])
    this.state.fileNa = e.target.files[0]
  }

  handleFormSubmit = (e) => {
    e.preventDefault();
    this.setState({isSubmittedForUpload : true})
    const formData = new FormData();

    formData.append("file", this.state.fileNa);
    formData.append("month", this.state.date10);

    const requestOptions = {
      method: "POST",
      body: formData
    };
    fetch("https://courier-manager.azurewebsites.net/uploader", requestOptions)
      .then((response) => {
        if(response.ok)
        return response.json()
        else {
          this.messages.show({
            severity: "error",
            summary: "Internal Server Error",
            detail: String("Please choose correct file"),
          });
          throw new Error(response.status);
        }
      })
      .then((data) => {
        console.log(data)
        this.setState({ varll: data.data });
        this.setState({isSubmittedForUpload : false})
        console.log(this.state.varll)
      }).catch((errror) => {this.setState({isSubmittedForUpload:false})});
  }
  render() {

    return (
      <div>
         <Toast ref={(el) => (this.messages = el)} />
        <h2></h2>
        <div className="p-formgroup-inline">
          <div className="p-field">
            <InputText id="lastname5" type="file" placeholder="Final Bill" onChange={(e) => this.sendFile(e)} />
          </div>
          {this.state.isSubmittedForUpload ? <Button type="button" label="Submit" loading loadingOptions={{ position: 'right' }} /> : <Button type="button" label="Submit" onClick={this.handleFormSubmit} />}
        </div>
        <SearchAnalyticsTable dataFromParent={JSON.stringify(this.state.varll)}></SearchAnalyticsTable>
      </div>
    );
  }
}

export default UploadFile;
